import Blogbtn from '../blogbtn/blogbtn'

function Blogcard(props){
  return(
    <a href={props.link}>
      <div className='blogcard'>
        <div className="imgdivblog" style={{
          width: '22.6rem',
          height: '21.6rem',
          flexShrink: '0',
          borderRadius: '24px',
          background: `url(${props.imgname}) lightgray 50% / cover no-repeat`,
          }}></div>
        <div dir='rtl' className="titleanddesccontioner">
          <p className="titleblogcard">{props.intro}</p>
          <p className="descblog">{props.desc}</p>
        </div>
        <Blogbtn intro='مـشـاهـده مـقـاله' />
        <div className="filerdecblog"></div>
      </div>
    </a>
  )
}

export default Blogcard