
import logo from '../../images/logo.png'
import Logbtn from '../logBtn/logbtn'
import Reqbtn from '../reqbtn/reqbtn'
import { Link } from 'react-scroll';


function Footer(props){
  return(
    <div id='Footer' className="Footer">
      <div className="itemFOT btnfotinfo">
          <Link to='sec1' smooth={true} duration={1000}>
            <button></button>
          </Link>
      </div>
      <div className="itemFOT containfot">
        <div className="itemcontionR">
          <img loading='lazy' className='itemR itemrlogo' src={logo} alt="logo" />
          <div dir='rtl' className="itemR">همین الان عضو <p>همکلاسیم</p> شو! آینده فرزندت مهم ترین ثمره زندگیته!</div>
          <div className="itemR itemRinfofoot">توسعه: تیم دیجیتال مارکتینگ وبکیو</div>
        </div>
        <div className="itemcontionL">
          <Logbtn link='#' intro='ورود به همکلاسیم' />
          <Reqbtn link='#' intro='درخواست ثبت نام'/>
          </div>
      </div>
      <hr />
      <div className="itemFOT foot">
        <div className="fotlist">
          <Link smooth={true} duration={1000} to='sec1' className="fotlistitem">صفحه اصلی</Link>
          <Link smooth={true} duration={1000} to='sec3' className="fotlistitem">تماس با ما</Link>
          <Link smooth={true} duration={1000} to='sec5' className="fotlistitem">درباره ما</Link>
          <a href={props.loglink} className="fotlistitem">ورود به سیستم</a>
        </div>
        <div dir='rtl' className="fotcomp">تمامی حقوق کپی رایت متعلق به <a className='linkfot' href={props.link}>مجموعه دیجیتال مارکتینگ وبکیو</a> میباشد.</div>
      </div>
    </div>
  )
}

export default Footer