
import Blogbtn from '../blogbtn/blogbtn'
import {ReactComponent as Hilight} from '../../images/blubackhi.svg'
import {ReactComponent as Icon} from '../../images/Iconbtnwhite.svg'
import {ReactComponent as ChatIcon} from '../../images/chaticon.svg'
import {ReactComponent as Dim} from '../../images/dimound.svg'
import {ReactComponent as Oooo} from '../../images/iconoooo.svg'
import {ReactComponent as Newferfer} from '../../images/newferferi.svg'
import {ReactComponent as Khatkhat} from '../../images/khatkhat.svg'
import lbag from '../../images/baglighit.png'

function Sec5(props){
  return(
    <div id='sec5' className="sec5">
      <div className="aboutdiv">
        <Khatkhat className='Khatkhat' />
        <Dim className='iconsec5bg2' />
        <Oooo className='iconsec5bg1' />
        <Newferfer className='iconsec5bg3' />
        <img src={lbag} alt="lbag" className='lbag' />
        <div className="cornertitle">دربــاره هـــمکلاسیـــــم</div>
        <Hilight className='bluehiback'/>
        <div dir='rtl' className="abouttitle">یه کیف پر از خدمات!</div>
        <p dir='rtl' className="descaboutsec5">سیستم مــدیریت مــدرسه همکلاسیم در سال ۱۴۰۲ توسط تیــم دیـــجـــیتــال مارکتینگ وبکیو توسعه و تــولید شد و با جهت افزایش بهــره وری مـــدارس غیـر انتــفاعـــــی در اختــیار این مدارس قرار گرفت.</p>
        <Blogbtn class='btnTransAnimition' intro='درباره همکلاسیم!' link='#'  />
      </div>
      <div className="doyodiv">
        <div className='doyowant'>
          <div className="farmedoyowantinp">
            <input dir='rtl' type="text"  id='inpforjoin' placeholder='اینجا شماره تماست رو وارد کن...'/>
            <Icon onClick={props.clicked} className='iconbuttennum' />
          </div>
          <ChatIcon className='chaticon' />
          <div dir='rtl' className="doyotitle">میخوای ثبت نام کنی؟</div>
          <div dir='rtl' className="descdoyowant">اگه فکر میکنی میخوای برای آیـنـــده فرزدنـــت همکلاسیم رو انــتخـــاب کـــنی تا شاهد رشد و شکوفایی اون باشی میتونی توی باکـــس زیـر شماره تماست رو بــــذاری تا مــــا باهــات تماس بگیریم.</div>
        </div>
      </div>
    </div>
  )
}

export default Sec5