
import React from "react";
import { ReactComponent as Icon1 } from "../../images/home-icon.svg";
import { ReactComponent as Icon2 } from "../../images/file-text_clean.svg";
import { ReactComponent as Icon3 } from "../../images/Phone_call.svg";
import {Link} from 'react-scroll'

const iconList = [<Icon1 className="icon_wrapper" />,<Icon2 className="icon_wrapper" />, <Icon3 className="icon_wrapper" />];

function Navbtn(props) {
  function activeHandel(e){
    const parent = e.currentTarget.parentNode.parentNode.children
    for (let element of parent){
      element.children[0].children[0].classList.remove('active_icon_wrapper')
      element.children[0].classList.remove('btn_active')
    }
    const icon = e.currentTarget.children
    icon[0].classList.add('active_icon_wrapper')
    e.currentTarget.classList.add('btn_active')
  }
  return (
    <Link to={props.link} smooth={true} duration={props.duration} >
      <div className="btn_nav" onClick={activeHandel}>
          {props.intro}
          {iconList[Number(props.icon) - 1]}
      </div>
    </Link>
  );
}

export default Navbtn;
