
import {ReactComponent as Iconlink} from '../../images/LinkIcon.svg'

function Option(props) {
  return (
    <div className='option'>
        <label>{props.intro}</label>
        <div className="toside">
            <a href={props.link}>
              <Iconlink width={44} height={44}/>
            </a>
            <img loading='lazy' width={props.imgwidth} height={props.height} src={props.photoName} alt={props.alt} />
        </div>
    </div>
  )
}

export default Option;
