
import { ReactComponent as Icon } from '../../images/login-icon.svg';

function Logbtn(props){
    return(
        <a href={props.link}>
            <button className='logbtn'>
                <div className="iconlogbtn">
                    <Icon />
                </div>
                {props.intro}
            </button>
        </a>
    )
}

export default Logbtn