
import Option from "../option/option";
import bag from '../../images/bag.png'
import toop from '../../images/toop.png'
import pink from '../../images/pink.png'
import zhim from '../../images/zhim.png'
import info from '../../images/infofram.png'
import modir from '../../images/modirmadrese.png'
import {ReactComponent as Tagicon} from '../../images/tagicon.svg'
import {ReactComponent as Iconlink} from '../../images/LinkIcon.svg'


function Sec2(props) {
  return (
    <div id="sec2">
      <div className="sec2">
        <div className="infodivsec3">
          <img loading='lazy' className="infoframe" src={info} alt="info" />
          <p className="textinfo texttopinfo">۲۸ سـال
ســابـــقـه مدریریت</p>
          <p className="textinfo textbotteminfo">مدیر نمونه مــدارس در ســـال ۱۴۰۲</p>
          <img loading='lazy' src={modir} alt="modir" className="modirimg"/>
          <Tagicon className="tagicon" />
          <div className="modirname">سید محمد جواد هاشمی</div>
          <a href="#info">
            <Iconlink className="iconlinkinfo animitionIconHower" />
          </a>
        </div>
        <div className="listOption">
            <Option link="#" intro='کــــــــــــیف در مدرسه' photoName={bag} alt='bag' imgwidth={88} imgheight={64}/>
            <Option link="#" intro='آمــــــــــــوزش والیبال' photoName={toop} alt='toop' imgwidth={88} imgheight={64}/>
            <Option link="#" intro='آموزش ژیمناستیک' photoName={zhim} alt='zhim' imgwidth={88} imgheight={64}/>
            <Option link="#" intro='آمــــــــــــوزش شــــــــنا' photoName={pink} alt='pink' imgwidth={88} imgheight={64}/>
        </div>
      </div>
    </div>
  );
}

export default Sec2;
