

function Button(props){
    return(
        <a href={props.link}>
            <button className="fbtninpage" onClick={props.onClick}>
                <svg className="svgbuttom" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M11.68 14.62L14.24 12.06L11.68 9.5" stroke="#292D32" strokeWidth="1.5" strokeMiterlimit="10" strokeLinecap="round" strokeLinejoin="round"/>
                    <path d="M4 12.06H14.17" stroke="#292D32" strokeWidth="1.5" strokeMiterlimit="10" strokeLinecap="round" strokeLinejoin="round"/>
                    <path d="M12 4C16.42 4 20 7 20 12C20 17 16.42 20 12 20" stroke="#292D32" strokeWidth="1.5" strokeMiterlimit="10" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
                <label className="btnlable" content={props.intro}>{props.intro}</label>
            </button>
        </a>
    )
}

export default Button