import { ReactComponent as Iconlink } from "../../images/LinkIcon.svg";
import { ReactComponent as Iconlinksec4 } from "../../images/Iconlinksec4.svg";
import Iconbook from "../../images/booksec4.png";
import Iconload from "../../images/Loadersec4.png";
import Iconnews from "../../images/newssec4.png";
import Iconteacher from "../../images/teachersec4.png";
import Iconmembers from "../../images/peoplesec4.png";
import Blogcard from "../blogcard/blogcard";
import React, { useEffect, useState } from "react";
import parse from "html-react-parser";
import Catbtn from "../categories_btn/CatBtn";

function Sec4(props) {
  const iconsNames = [Iconload, Iconbook, Iconnews, Iconteacher, Iconmembers];
  const [bloglist, setbloglist] = useState([]);
  const [blogfilter, setfillter] = useState(1);
  const [blogdata, setblogdata] = useState({});
  const [blogapi, setblogapi] = useState([]);
  const [categori, setcatgori] = useState([[1, "همه"]]);
  async function getdata() {
    const getcat = await fetch(
      "https://www.amirhossein-dev.ir/wp-json/wp/v2/categories/",
      { cache: "no-store" }
    );
    const catdata = await getcat.json();
    catdata.forEach((dataRow, index) => {
      !categori.includes([Number(dataRow.id), dataRow.name])
        ? setcatgori((oldlist) => [
            ...oldlist,
            [Number(dataRow.id), dataRow.name],
          ])
        : [null].map((x) => x);
    });
    setcatgori((x) => x.slice(0, 5));
    const res = await fetch(
      "https://www.amirhossein-dev.ir/wp-json/wp/v2/posts?per_page=5",
      { cache: "no-store" }
    );
    const data = await res.json();
    setblogapi(data);
  }
  async function savedata(listdata) {
    listdata.forEach(async (element, index) => {
      const linkmedia = element._links["wp:featuredmedia"][0]["href"];
      const res = await fetch(linkmedia);
      const datares = await res.json();
      const description = String(await element.excerpt.rendered).replace(
        "\n",
        ""
      );
      const getcatres = await blogapi[index].categories;

      const myobj = {
        [index]: {
          title: await element.title.rendered,
          desc: parse(description).props?.children,
          link: await element.link,
          imgsrc: await datares.guid.rendered,
          categories: getcatres,
        },
      };
      if (Object.keys(myobj).length) {
        setblogdata((prevData) => Object.assign({}, prevData, myobj));
      }
    });
  }

  function updaterBlogList() {
    Object.keys(blogdata).map((x, index) => {
      if (
        Object.keys(blogdata).length > 4 &&
        blogdata[index].categories.includes(blogfilter)
      ) {
        return (
          <Blogcard
            key={index}
            intro={Object.keys(blogdata).length ? blogdata[index].title : ""}
            link={Object.keys(blogdata).length ? blogdata[index].link : ""}
            imgname={Object.keys(blogdata).length ? blogdata[index].imgsrc : ""}
            desc={Object.keys(blogdata).length ? blogdata[index].desc : ""}
          />
        );
      }
    });
  }
  function itemcatlistclick(e, fillter = 1) {
    setfillter(Number(fillter));
    const imgintoitem = e.currentTarget.children[1];
    let child = e.currentTarget.parentNode.children;
    let [...orther] = child;
    orther.pop();
    for (let element of orther) {
      element.classList.remove("catlistactive");
      element.children[1].classList.remove("iconon");
    }
    imgintoitem.classList.add("iconon");
    e.currentTarget.classList.add("catlistactive");
  }

  useEffect(() => {
    getdata();
  }, []);

  useEffect(() => {
    if (blogapi.length) {
      savedata(blogapi);
    }
  }, [blogapi]);

  return (
    <div
      id="sec4"
      className="sec4"
      onClick={() => {
        // get();
      }}
    >
      <div className="subtitlesec4">مـقـــالات هـــمــکلاسیم</div>
      <div className="subsec4quest">
        در هــــمــــکــلاســـیم چـــه مـــیــگـــــــذرد؟
      </div>
      <a href={props.linkmore}>
        <Iconlink className="iconlinksec4 animitionIconHower" />
      </a>
      <div className="catgorylist">
        {categori.map((datas, index) => {
          return (
            <Catbtn
              key={index}
              fillter={datas[0]}
              onClick={(e) => {
                itemcatlistclick(e, datas[0]);
              }}
              intro={datas[1]}
              class={index == 0 ? "catlistsec4 catlistactive" : "catlistsec4"}
              iconsrc={iconsNames[index]}
              iconclass={index == 0 ? "iconsec4btns iconon" : "iconsec4btns"}
            />
          );
        })}

        <div className="catlistsec4 morebtncat">
          <a href={props.linkcat}>
            <button className="btnmorecat">
              <Iconlinksec4 />
            </button>
          </a>
        </div>
      </div>
      <div className="blogcontiner">
        {(Object.keys(blogdata).map((x, index) => {
          if (
            Object.keys(blogdata).length > 4 && (blogfilter!==1 ? blogdata[index].categories.includes(blogfilter):true)
          ) {
            return (
              <Blogcard
                key={index}
                intro={
                  Object.keys(blogdata).length ? blogdata[index].title : ""
                }
                link={Object.keys(blogdata).length ? blogdata[index].link : ""}
                imgname={
                  Object.keys(blogdata).length ? blogdata[index].imgsrc : ""
                }
                desc={Object.keys(blogdata).length ? blogdata[index].desc : ""}
              />
            );
          }
        })).slice(0,4).map(x=>x)}
      </div>
    </div>
  );
}

export default Sec4;
