
import {ReactComponent as Iconlink} from '../../images/Iconbtnwhite.svg'


function Blogbtn(props){
    return(
            <div className={props.class?"btnframeblog "+props.class:'btnframeblog'}>
                <Iconlink />
                <button dir="rtl" className="btnblog">
                    {props.intro}
                </button>
            </div>
    )
}

export default Blogbtn