import logo from "../../images/logo.png";
import Button from "../buttom/buttom";
import NavBtn from "../nav-btn/nav-btn";
import gilrimg from "../../images/girlimg.png";
import smile from "../../images/smile.png";
import ferferi from "../../images/ferferh.png";
import hamcelasi from "../../images/hamkelas.png";
import star from "../../images/star.png";
import Logbtn from "../logBtn/logbtn";
import smallback from "../../images/small_back.png";
import contact from "../../images/contact.png";
import { useEffect, useState } from "react";

function Sec1() {
  function handelop(e) {
    const intodiv =
      document.getElementsByClassName("navbar")[0].children[2].children[0];
    intodiv.classList.add("btn_active");
    intodiv.children[0].classList.add("active_icon_wrapper");
  }

  useEffect(() => {
    handelop();
  }, []);

  return (
    <div>
      <div id="sec1">
        <header>
          <Button classLis="btnf1sec1" link="#" intro="ورود به سیستم" />
          <div className="navbar">
            <NavBtn
              duration={1000}
              active="true"
              link="sec3"
              intro="تماس با ما"
              icon="3"
            />
            <NavBtn
              duration={1000}
              active="false"
              link="sec5"
              intro="درباره ما"
              icon="2"
            />
            <NavBtn
              duration={1000}
              active="false"
              link="sec1"
              intro="صفحه اصلی"
              icon="1"
            />
          </div>
          <img loading="lazy" src={logo} alt="logo" />
        </header>
        <div className="girlimg">
          <img loading="lazy" src={gilrimg} alt="girlImg" />
        </div>
        <img
          loading="lazy"
          src={star}
          alt="star vector"
          className="starVector star1"
        />
        <img
          loading="lazy"
          src={star}
          alt="star vector"
          className="starVector star2"
        />
        <img
          loading="lazy"
          src={star}
          alt="star vector"
          className="starVector star3"
        />
        <img
          loading="lazy"
          className="smileIcon"
          src={smile}
          alt="smile icon"
        />
        <img loading="lazy" className="ferferi" src={ferferi} alt="ferferi" />
        <img
          loading="lazy"
          className="hamcelasi"
          src={hamcelasi}
          alt="hamcelasi"
        />
        <p dir="rtl" className="desp desktitle">
          همکلاسیم
        </p>
        <p dir="rtl" mio="siafiashiufhiasi" className="desp deskparagraph">
          بــا جـــدیـــدتــرین فنــاوری هــا و روش هــای آمــوزش، تــحـصیـلــی
          پــویـــا و مــــفــرح بــرای یـادگیری دانــش آمـوزان فـــراهـــم
          میـکنـد.
        </p>
        <p className="tdesc" dir="rtl">
          <span>همـــــــکلاسیـــــــــم </span>
          مـدرســــه ای بــرای خلاقیت و نوآوری!
        </p>
        <div className="Logbtn">
          <Logbtn link="#" intro="ورود به همکلاسیم" />
        </div>
        <img
          loading="lazy"
          className="smallback"
          src={smallback}
          alt="smallback"
        />
        <div className="footersec1">
          <img loading="lazy" src={contact} alt="contact" />
        </div>
      </div>
    </div>
  );
}

export default Sec1;
